<template>
  <div class="update_pto">
    <van-nav-bar
      class="toolnav"
      left-text="取消"
      right-text="完成"
      @click-left="$emit(`close`)"
      @click-right="onPhotoDone"
    />
    <div>
      <img :src="src" class="photo" ref="image" />
    </div>
  </div>
</template>

<script>
import { updatePhoto } from '@/api/profile.js';

import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';

export default {
  name: 'UpdatePhoto',
  props: {
    file: {
      type: File,
      required: true,
    },
  },
  data() {
    return {
      src: window.URL.createObjectURL(this.file), // 生成blob地址
      cropper: null,
    };
  },
  methods: {
    updateUserPhoto() {
      return new Promise((res) => {
        this.cropper.getCroppedCanvas().toBlob((file) => {
          res(file);
        });
      });
    },
    // 上传更新头像
    async onPhotoDone() {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
        duration: 0,
      });
      const formData = new FormData();
      //   formData.append("photo", this.file);
      const blob = await this.updateUserPhoto();

      formData.append('photo', blob);
      const { data } = await updatePhoto(formData);
      this.$emit('input', data.data.photo);
      this.$emit('close');
      this.$toast.success('保存成功');
    },
  },
  created() {},
  mounted() {
    const img = this.$refs.image;
    this.cropper = new Cropper(img, {
      viewMode: 1,
      dragMode: 'move',
      aspectRatio: 1,
      autoCropArea: 1,
      cropBoxMovable: false, // 裁切框是否可拖到
      cropBoxResizable: false, // 裁切框是否可改变大小
      background: false, // 是否有背景
      movable: true, // 图片是否可移动
    });
  },
};
</script>

<style lang="less" scoped>
.toolnav {
  background-color: #1989fa;
  /deep/.van-nav-bar__text {
    color: #fff;
  }
}
.photo {
  width: 100%;
  max-width: 100%;
  height: auto;
}
</style>
